.page-footer {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 30px 0;
}

.page-footer .page-share {
  text-align: left;
	margin-left: 15%;
}

.page-footer .page-share a {
  display: inline-block;
  text-decoration: none;
  background-color: #fafafa;
  border: 1px solid #ecf0f1;
  padding: 5px 10px;
  margin-left: 5px;
  color: #263959;
  font-size: 12px;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
}

.page-footer .page-share a:first-child {
  margin-left: 0;
}

.page-footer .page-share a:hover {
  background-color: #ecf0f1;
}

.page-footer .page-tag a {
  text-decoration: none;
  font-size: 10px;
  padding: 5px 10px;
  margin-left: 5px;
  color: #263959;
  text-transform: uppercase;
  border: 1px solid #263959;
}

.page-footer .page-tag a:hover {
  text-decoration: underline;
}


.button--large {
	padding: 15px 20px 16px;
	font-size: 13px;

	@include mq(tabletp) {
		padding: 19px 40px 20px;
		font-size: 17px;
	}
}

.button--light {
	background: transparent;
	border-color: $text-dark-color;
	color: $text-dark-color;

	&:hover {
		background: $accent-color;
		border-color: $accent-color;
		color: $background-color;
	}
}


hr {
	margin: 40px 0;
	border: 0;
	height: 2px;
	background: $border-color;
}


::selection 	 { background: $accent-color; color: $background-color; }
::-moz-selection { background: $accent-color; color: $background-color; }



// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - Page loading

.page-loader {
	display: none;
}

.page {
	@include transition(all 0.6s cubic-bezier(.87,-.41,.19,1.44));
}

body.loading {

	.loader {
		opacity: 1;
		visibility: visible;
		@include transition(all 0.4s ease-in-out 1s);
	}

	.page {
		opacity: 0;
		visibility: hidden;
		@include transition(all 0.4s ease-in-out, transform 0s ease-in-out 0.4s);
		@include translate(0,20px);
	}

	.footer {
		opacity: 0;
		visibility: hidden;
		@include transition(all 0.4s ease-in-out);
	}
}

.loader {
	position: fixed;
	top: 50%;
	left: 50%;
	@include translate(-50%,-50%);
	width: 60px;
	height: 15px;

	opacity: 0;
	visibility: hidden;
	@include transition(all 0.2s ease-in-out);

	svg {
		width: 100%;
		height: 100%;
		fill: rgba($text-dark-color,0.2);
	}
}

.page,
.page__content {
	float: left;
	clear: both;
	width: 100%;
}



// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - Page sections

.featured-image {
	position: relative;
	width: 100%;
	height: 0;
	padding-bottom: 56.25%;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@include mq(tabletl) {
		position: fixed;
		z-index: 10;
		top: 0;
		left: 0;
		bottom: 0;
		height: auto;
		padding-bottom: 0;
		width: 30%;
	}
}

section {
	margin-top: 40px;

	@include mq(tabletp) {
		margin-top: 50px;
	}

	@include mq(tabletl) {
		margin-top: 60px;
	}

	@include mq(laptop) {
		margin-top: 80px;
	}
}

.wrap {
	position: relative;
	margin: 0 auto;
	padding: 0 20px;

	@include mq(tabletp) {
		padding: 0 80px;
	}

	@include mq(tabletl) {
		padding: 0 120px;
	}

	@include mq(laptop) {
		padding: 0 140px;
	}

	@include mq(desktop) {
		padding: 0 180px;
	}
}



// - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - Buy CTA

.buy-cta {
	position: fixed;
	bottom: 0;
	right: 0;
}

.buy-cta__link {
	display: block;
	position: relative;
	width: 40px;
	height: 40px;
	background: #dc5b5d;

	@include mq(tabletp) {
		width: 60px;
		height: 60px;
	}

	svg {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 20px;
		height: 20px;
		@include translate(-50%,-50%);
		fill: #fff;

		@include mq(tabletp) {
			width: 30px;
			height: 30px;
		}
	}

	&:hover {

		.buy-cta__text {
			@include translate(0,0);
			opacity: 1;
		}
	}
}

.buy-cta__text {
	position: absolute;
	top: 0;
	right: 40px;
	background: #dc5b5d;
	padding: 14px 10px 13px 17px;
	color: #fff;
	font-size: 13px;
	line-height: 1;
	white-space: nowrap;

	@include transition(all 0.15s ease-in-out);
	@include translate(0,20px);
	opacity: 0;

	@include mq(tabletp) {
		right: 60px;
		padding: 22px 14px 23px 25px;
		font-size: 15px;
		@include translate(0,30px);
	}
}

.code-header {
    position: absolute;
    display: block;
	height: 0;
    z-index: 2;
    right: 24.25%;
    @include mq(tabletp) {
        //potentially change font size via here
		right: 10%;
		margin-block-start: 35px;
      }
    @include mq(tabletl) {right: 21%;margin-block-start: 35px;}
    @include mq(laptop) {right: 23%;margin-block-start: 45px;}
    @include mq(desktop) {right: 25%;margin-block-start: 45px;}
  }
  
  .copy-code-button {
    display: none;
    position: relative;
    grid-auto-flow: column;
    align-items: center;
    grid-column-gap: 4px;
    border: solid;
    border-color: $accent-color;
    border-radius: 10px;
    border-width: 1px;

    cursor: pointer;
    font-size: 18.7;
    color: $text-medium-color;
    padding: 4px 8px;
    background: #f0f0f0;
  
    &::before {
      content: "Copy";
    }
  
    &::after {
      content: "📋";
      display: block;
    }
  
    // This class will be toggled via JavaScript
    &.copied {
      &::before {
        content: "Copied!";
      }
  
      &::after {
        content: "✔️";
      }
    }
    :hover > & {
      //background-color: #f0f0f0;
      background: adjust-hue($accent-color,5%);
      color: $text-dark-color;
      border-color: adjust-hue($accent-color,5%)
      }
  }
  @media screen and (min-width: 768px) {
    .copy-code-button {
      display: grid;
    }
  }
  
